@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap&subset=cyrillic);
*{font-family:"Roboto", sans-serif;margin:0;padding:0}.container{min-width:calc(100vw - 0 * 2);max-width:1440px;min-height:calc(100vh - 0 * 2);padding:0;position:relative;z-index:1}


.home{background:url(/Wheel-It/static/media/start_page_bg.c6a0c237.svg) no-repeat 50% 50%;background-size:cover;position:relative;min-width:calc(100vw - 30px * 2);max-width:1440px;min-height:calc(100vh - 30px * 2);padding:30px;position:relative;z-index:1}.home .title{font-weight:bold;font-size:64px;line-height:75px;text-transform:uppercase;color:#363636;text-align:center;margin-top:50px}.home .description{font-style:normal;font-weight:normal;font-size:24px;line-height:28px;text-align:center;color:#363636;margin-top:22px}.home .start-btn{display:block;width:150px;height:150px;position:absolute;bottom:calc(31% - 150px / 2);left:calc(50% - 150px / 2);background:white;box-shadow:3px 3px 5px rgba(0,0,0,0.25);border:none;border-radius:100px;font-weight:bold;font-size:24px;line-height:150px;text-align:center;color:#ed6a5a;-webkit-transition:.3s;transition:.3s}.home .start-btn:hover,.home .start-btn:focus{cursor:pointer;box-shadow:10px 10px 10px rgba(0,0,0,0.25);-webkit-transition:.3s;transition:.3s;outline:0}.home .start-btn:active{box-shadow:0px 0px 0px rgba(0,0,0,0.25);-webkit-transition:0;transition:0}

.navigation{position:absolute;top:30px;right:30px;z-index:99}.navigation-list{list-style:none;display:flex}.navigation-list__item{margin-left:20px}

.wheel{min-width:calc(100vw - 30px * 2);max-width:1440px;min-height:calc(100vh - 30px * 2);padding:30px;position:relative;z-index:1;display:flex;justify-content:center;align-items:center}.wheel-arrow{position:relative}.wheel-arrow:before{content:"";background:#916dd5;position:absolute;top:-5px;left:50%;width:15px;height:15px;background:url(/Wheel-It/static/media/wheel_arrow.17aed943.svg) no-repeat 50% 50%;margin-left:-7px;z-index:100}.wheel-btn{width:100px;height:100px;line-height:100px;font-weight:bold;font-size:24px;text-align:center;color:#363636;background:#ffffff;box-shadow:4px 4px 10px rgba(0,0,0,0.25);position:absolute;top:calc(50% - 100px / 2);left:calc(50% - 100px / 2);border-radius:50%;border:none;text-transform:uppercase;-webkit-transition:0.2s;transition:0.2s}.wheel-btn:hover{cursor:pointer;box-shadow:10px 10px 10px rgba(0,0,0,0.25);-webkit-transition:0.2s;transition:0.2s}.wheel-btn:focus{outline:none}.wheel-btn:active{box-shadow:0 0 0 rgba(0,0,0,0);-webkit-transition:0.2s;transition:0.2s}.wheel-btn:disabled{color:#ffffff;-webkit-transition:0.2s;transition:0.2s}@media screen and (max-width: 480px){.wheel-btn{font-size:18px;width:70px;height:70px;line-height:70px;top:calc(50% - 70px / 2);left:calc(50% - 70px / 2)}}

